.expense-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
  margin: 1rem 0;
  border-radius: 15px;

  background: linear-gradient(
    135deg,
    rgba(244, 115, 64, 0.9335084375547094) 7%,
    rgba(239, 47, 136, 1) 58%,
    rgba(84, 109, 242, 0.9363095580028886) 92%
  );
}

.expense-item__description {
  display: flex;
  flex-direction: column;
  gap: 1rem;
  align-items: flex-end;
  flex-flow: column-reverse;
  justify-content: flex-start;
  flex: 1;
}

.expense-item h2 {
  color: #3a3a3a;
  font-size: 1rem;
  flex: 1;
  margin: 0 1rem;
  color: white;
}

.expense-item__price {
  font-size: 1rem;
  font-weight: bold;
  color: white;
  background: #ef2f88;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
  border: 2px solid #f47340;
  padding: 0.5rem;
  border-radius: 15px;
}

@media (min-width: 580px) {
  .expense-item__description {
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    flex: 1;
  }

  .expense-item__description h2 {
    font-size: 1.25rem;
  }

  .expense-item__price {
    font-size: 1.25rem;
    padding: 0.5rem 1.5rem;
  }
}
