@import url(https://fonts.googleapis.com/css2?family=Noto+Sans+JP:wght@400;700&display=swap);
* {
  box-sizing: border-box;
  padding: 0px;
  margin: 0px;
}

html {
  font-family: "Noto Sans JP", sans-serif;
}

body {
  margin: 0;
  background-color: rgb(228, 228, 228);
}

h1 {
  text-align: center;
}

.new-expense__controls {
  display: flex;
  flex-wrap: wrap;
  grid-gap: 1rem;
  gap: 1rem;
  margin-bottom: 1rem;
  text-align: left;
}

.new-expense__control label {
  font-weight: bold;
  color: white;
  margin-bottom: 0.5rem;
  display: block;
}

.new-expense__control input {
  box-shadow: 0 2px 6px rgba(0, 0, 0, 0.25);
  background: #ffffff;
  font: inherit;
  padding: 0.5rem;
  border-radius: 16px;
  border: 1.5px solid #F9D371;
  width: 20rem;
  max-width: 100%;
}

.new-expense__actions {
  text-align: right;
}

.new-expense {
  background: linear-gradient(132deg, rgba(244,115,64,0.8942927512801996) 15%, rgba(249,211,113,0.9643207624846813) 100%);
  padding: 1rem;
  margin: 2rem auto;
  width: 50rem;
  max-width: 95%;
  border-radius: 15px;
  text-align: center;
  box-shadow: 0 2px 11px rgba(0, 0, 0, 0.25);
}

.new-expense button {
  font: inherit;
  cursor: pointer;
  padding: 0.5rem 0.5rem;
  border: 1.5px solid #f4b540;
  background-color: #F47340;
  color: white;
  border-radius: 13px;
  margin-right: 1rem;
  box-shadow: 0 2px 5px rgba(0, 0, 0, 0.25);
}

.new-expense button:hover {
  background: linear-gradient(0deg, rgba(244,115,64,1) 0%, rgba(239,47,136,1) 85%);
}

.new-expense button.alternative {
  color: #220131;
  border-color: transparent;
  background-color: transparent;
}

.new-expense button.alternative:hover,
.new-expense button.alternative:active {
  background-color: #ddb3f8;
}
.expenses-filter {
  color: white;
  padding: 0 1rem;
}

.expenses-filter__control {
  display: flex;
  width: 100%;
  align-items: center;
  justify-content: space-between;
  margin: 1rem 0;
}

.expenses-filter label {
  font-weight: bold;
  margin-bottom: 0.5rem;
}

.expenses-filter select {
  font: inherit;
  padding: 0.5rem 3rem;
  font-weight: bold;
  border-radius: 6px;
}

.expense-date {
  display: flex;
  flex-direction: column;
  width: 5rem;
  height: 5rem;
  border: 2px groove #F47340;
  background-color: #752be4;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);

  color: white;
  border-radius: 10px;
  align-items: center;
  justify-content: center;
}

.expense-date__month {
  font-size: 0.75rem;
  font-weight: bold;
}

.expense-date__year {
  font-size: 0.7rem;
}

.expense-date__day {
  font-size: 1.5rem;
  font-weight: bold;
}

.expense-item {
  display: flex;
  justify-content: space-between;
  align-items: center;
  padding: 0.5rem;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
  margin: 1rem 0;
  border-radius: 15px;

  background: linear-gradient(
    135deg,
    rgba(244, 115, 64, 0.9335084375547094) 7%,
    rgba(239, 47, 136, 1) 58%,
    rgba(84, 109, 242, 0.9363095580028886) 92%
  );
}

.expense-item__description {
  display: flex;
  flex-direction: column;
  grid-gap: 1rem;
  gap: 1rem;
  align-items: flex-end;
  flex-flow: column-reverse;
  justify-content: flex-start;
  flex: 1 1;
}

.expense-item h2 {
  color: #3a3a3a;
  font-size: 1rem;
  flex: 1 1;
  margin: 0 1rem;
  color: white;
}

.expense-item__price {
  font-size: 1rem;
  font-weight: bold;
  color: white;
  background: #ef2f88;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
  border: 2px solid #f47340;
  padding: 0.5rem;
  border-radius: 15px;
}

@media (min-width: 580px) {
  .expense-item__description {
    flex-direction: row;
    align-items: center;
    justify-content: flex-start;
    flex: 1 1;
  }

  .expense-item__description h2 {
    font-size: 1.25rem;
  }

  .expense-item__price {
    font-size: 1.25rem;
    padding: 0.5rem 1.5rem;
  }
}

.expenses-list {
  list-style: none;
  padding: 0;
}

.expenses-list__fallback {
  color: white;
  text-align: center;
}

.chart-bar {
  height: 100%;
  display: flex;
  flex-direction: column;
  align-items: center;
}

.chart-bar__inner {
  height: 100%;
  width: 100%;
  border: 1.5px solid #313131;
  border-radius: 12px;
  background-color: #d1bdee;
  overflow: hidden;
  display: flex;
  flex-direction: column;
  justify-content: flex-end;
}

.chart-bar__fill {
  background-color: #8843F2;
  width: 100%;
  transition: all 0.3s ease-out;
}

.chart-bar__label {
  font-weight: bold;
  font-size: 0.5rem;
  text-align: center;
}

.chart {
    padding: 1rem;
    border-radius: 12px;
    background-color: #f8dfff;
    text-align: center;
    display: flex;
    justify-content: space-around;
    height: 10rem;
  }
.expenses {
  padding: 1rem;
  background-color: #F9D371;
  margin: 2rem auto;
  width: 50rem;
  max-width: 95%;
  border-radius: 15px;
  box-shadow: 0 2px 8px rgba(0, 0, 0, 0.25);
}

